.auth__main {
    align-items: center;
    background-color: rgb(14, 14, 66);
    display: flex;
    justify-content: center;
    margin: 0px;
    height: 100vh;
    width: 100vw;
  }
  
  .auth__box-container {
    background-color: white;
    box-shadow: 0px 3px grey;
    border-radius: 2px;
    padding: 20px;
    width: 250px;
  }
  
  
  .auth__title {
    color: rgb(9, 9, 61);
    margin-bottom: 20px;
  }
  
  .auth__input {
    color: grey;
    border: 0px;
    border-bottom: 1px solid grey;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;
  
    transition: border-bottom .3s ease;
    
   
  }
  
  .auth__social-networks {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
  }
  
  
  .auth__alert-error {
    background-color: red;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px
  }


